import { AfterContentInit, OnDestroy, QueryList } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { ConfigurationService } from "./configuration.service";
import { Change } from "./configuration.service";
import { CollectionService, Item, ItemChange } from "./collection.service";

/**
 * @hidden
 */
export abstract class CollectionComponent implements AfterContentInit, OnDestroy {
  protected children: QueryList<Item>;
  private subscription: Subscription;
  private items: any[] = [];

  constructor(protected configKey: string,
              protected configurationService: ConfigurationService,
              protected collectionService: CollectionService) {
    this.subscription = collectionService.onItemChange$.subscribe(
        changes => this.processChanges(changes)
    );
  }

  public ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  public ngAfterContentInit(): void {
    this.readItems();
    this.children.changes.subscribe(() => this.readItems());
  }

  private processChanges(changes: ItemChange): void {
      if (!this.children) {
          return;
      }

      const index = this.children.toArray().indexOf(changes.sender);
      if (index < 0) {
          return;
      }

      this.items[index] = changes.options;
      this.change();
  }

  private readItems(): void {
    this.items = this.children.map(s => s.options);
    this.change();
  }

  private change(): void {
      this.configurationService.notify(
          new Change(this.configKey, this.items)
      );
  }
}



